import InitialDataState from 'util/InitialDataState'
import ObjectsCopier from 'util/ObjectsCopier'

function DataCombiner(data) {
  const result = ObjectsCopier(InitialDataState)

  for (const year in data) {
    if (Object.hasOwnProperty.call(data, year)) {
      result.total += data[year].total
      result.Android += data[year].Android
      result.iOS += data[year].iOS
      result.months.push(...data[year].months)
      result.dataByMonths.Android.push(...data[year].dataByMonths.Android)
      result.dataByMonths.iOS.push(...data[year].dataByMonths.iOS)
    }
  }

  return result
}

export default DataCombiner
