import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import HowToRegIcon from '@material-ui/icons/HowToReg'
import Grid from '@material-ui/core/Grid'
import FilterByYearButtons from 'components/FilterByYearButtons'
import ComparisonTable from 'components/ComparisonTable'
import LineChart from 'components/LineChart'
import PieChart from 'components/PieChart'

const useStyles = makeStyles({
  charts: {
    marginBottom: 30
  }
})

export default function ActivityScreen({ data }) {
  const classes = useStyles()

  return (
    <>
      <FilterByYearButtons />

      <ComparisonTable data={data} TotalIcon={HowToRegIcon} />

      <Grid item xs={12} className={classes.charts}>
        <LineChart months={data.months} dataByMonths={data.dataByMonths} />
      </Grid>

      <Grid item xs={12} className={classes.charts}>
        <PieChart dataByPlatforms={[data.Android, data.iOS]} />
      </Grid>
    </>
  )
}
