import { useSelector } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles'
import theme from 'theme'

const useStyles = makeStyles({
  root: {
    width: '100%',
    position: 'absolute',
    bottom: '1em',
    left: '50%',
    color: '#9e9e9e',
    transform: 'translateX(-50%)',
    textIndent: '3em',
    fontSize: '.8em'
  },
  updateDateInfo: {
    position: 'absolute',
    right: 10,
    bottom: 0,
    color: theme.palette.success.main
  },
  hidden: {
    display: 'none'
  }
})

export default function InfoCardUpdateDate() {
  const classes = useStyles()
  const updateDate = useSelector((state) => state.app.lastUpdateDate)
  const daysPassed = useSelector((state) => state.app.increments.days)
  const yearFilter = useSelector((state) => state.app.filterByYear)

  return (
    <div className={classes.root}>
      {updateDate}{' '}
      <span
        className={
          daysPassed > 0 && yearFilter === 'total'
            ? classes.updateDateInfo
            : classes.hidden
        }
      >
        last <b>{daysPassed}</b> days
      </span>
    </div>
  )
}
