import React from 'react'
import { Line } from '@reactchartjs/react-chart.js'
import { purple } from '@material-ui/core/colors'

export default function LineChart({ months, dataByMonths }) {
  const totalDownloads = []
  for (let i = 0; i < dataByMonths.Android.length; i++) {
    totalDownloads.push(dataByMonths.Android[i] + dataByMonths.iOS[i])
  }

  const data = {
    labels: months,
    datasets: [
      {
        label: '',
        data: totalDownloads,
        backgroundColor: purple[500],
        fill: false,
        pointRadius: 6,
        pointHoverRadius: 10
      }
    ]
  }

  const options = {
    legend: {
      display: false
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true
          }
        }
      ]
    }
  }

  return <Line data={data} options={options} />
}
