function NumbersFormatter(number, divider = ' ') {
  const nums = number.toString().split('').reverse()
  const formatted = [nums[0]]
  for (let i = 1; i < nums.length; i++) {
    if (i % 3 === 0) formatted.push(divider)
    formatted.push(nums[i])
  }
  return formatted.reverse()
}

export default NumbersFormatter
