import Ajv from 'ajv'

function DataValidator(data, dataSchema = 'Amplitude') {
  const ajv = new Ajv()
  const schema = require(`./schemas/${dataSchema}DataSchema.json`)
  const validate = ajv.compile(schema)
  for (const year in data) {
    if (Object.hasOwnProperty.call(data, year)) {
      if (!validate(data[year])) {
        throw new Error(`${dataSchema} Update Error! Try again later`)
      }
    }
  }
}

export default DataValidator
