import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import AttachMoneyIcon from '@material-ui/icons/AttachMoney'
import InfoCard from 'components/InfoCard'
import FilterByYearButtons from 'components/FilterByYearButtons'
import ComparisonTable from 'components/ComparisonTable'
import LineChart from 'components/LineChart'
import GroupedBar from 'components/GroupedBar'
import PieChart from 'components/PieChart'

const useStyles = makeStyles({
  block: {
    marginTop: '1.5em'
  },
  standout: {
    marginTop: '1.5em',
    marginBottom: '1em'
  }
})

export default function SalesScreen({ data }) {
  const classes = useStyles()
  return (
    <>
      <Grid item xs={12} className={classes.standout}>
        <InfoCard platform="total" downloads={data.total} />
      </Grid>

      <Grid item xs={12}>
        <InfoCard platform="Android" downloads={data.Android} />
      </Grid>

      <Grid item xs={12}>
        <InfoCard platform="iOS" downloads={data.iOS} />
      </Grid>

      <FilterByYearButtons />

      <ComparisonTable
        data={data}
        TotalIcon={AttachMoneyIcon}
        reduceMillions={false}
      />

      <Grid item xs={12} className={classes.block}>
        <LineChart months={data.months} dataByMonths={data.dataByMonths} />
      </Grid>

      <Grid item xs={12} className={classes.standout}>
        <PieChart dataByPlatforms={[data.Android, data.iOS]} />
      </Grid>

      <Grid item xs={12} className={classes.block}>
        <GroupedBar months={data.months} dataByMonths={data.dataByMonths} />
      </Grid>
    </>
  )
}
