import './App.css'
import { useEffect } from 'react'
import LoginPage from './components/LoginPage'
import Pending from './components/Pending'
import Navigation from './components/Navigation'
import ErrorPage from './components/ErrorPage'
import ScreenComponent from './components/screens/ScreenComponent'
import Grid from '@material-ui/core/Grid'
import { makeStyles } from '@material-ui/core/styles'
import { useSelector, useDispatch } from 'react-redux'
import { fetchAmplitudeData } from './redux/reducer'

const useStyles = makeStyles({
  root: {
    width: '100%',
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto'
  }
})

function App() {
  const classes = useStyles()

  const pending = useSelector((state) => state.app.pending)
  const data = useSelector((state) => state.app.data)
  const dataType = useSelector((state) => state.app.dataType)
  const isLoggedIn = useSelector((state) => state.app.isLoggedIn)
  const serverError = useSelector((state) => state.app.serverError)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(fetchAmplitudeData())
  }, [dispatch])

  if (pending) return <Pending />
  if (!isLoggedIn) {
    return !serverError ? <LoginPage /> : <ErrorPage />
  }

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={12}>
          <Navigation />
        </Grid>

        <ScreenComponent type={dataType} data={data} />
      </Grid>
      <br />
    </div>
  )
}

export default App
