import React from 'react'
import { Bar } from '@reactchartjs/react-chart.js'
import theme from 'theme'

export default function GroupedBar({ months, dataByMonths }) {
  const data = {
    labels: months,
    datasets: [
      {
        label: 'Android',
        data: dataByMonths.Android,
        backgroundColor: theme.palette.platforms.Android
      },
      {
        label: 'iOS',
        data: dataByMonths.iOS,
        backgroundColor: theme.palette.platforms.iOS
      }
    ]
  }

  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true
          }
        }
      ]
    },
    legend: {
      display: false
    }
  }

  return <Bar data={data} options={options} />
}
