function NumbersMillionsReducer(arr) {
  if (!arr.filter(Boolean).length) return arr

  const isMinLengthExceeded =
    arr.filter((val) => !isNaN(parseInt(val))).length < 7
  const abbreviation = isMinLengthExceeded ? 'k' : 'm'
  let result = ''
  for (let i = 0; i < arr.length - 3; i++) {
    result += arr[i] ? arr[i] : ' '
  }

  return `${result.substring(0, result.length - 1)}${abbreviation}`
}

export default NumbersMillionsReducer
