import { createSlice } from '@reduxjs/toolkit'
import API from 'util/API'
import InitialDataState, { InitialIncrementsState } from 'util/InitialDataState'
import DataValidator from 'util/DataValidator'
import DataCombiner from 'util/DataCombiner'
import DateFormatter from 'util/DateFormatter'
import { error419Message, errorNetwork } from 'util/messages'

export const slice = createSlice({
  name: 'app',
  initialState: {
    code: '',
    pending: true,
    isLoggedIn: false,
    data: InitialDataState,
    dataByYears: {},
    dataType: 'downloads',
    filterByYear: 'total',
    increments: InitialIncrementsState,
    lastUpdateDate: null,
    serverError: false,
    serverErrorMessage: ''
  },
  reducers: {
    setCode: (state, action) => {
      state.code = action.payload
      localStorage.setItem('amp_code', action.payload)
    },
    setPending: (state, action) => {
      state.pending = action.payload
    },
    setIsLoggedIn: (state, action) => {
      state.isLoggedIn = action.payload
    },
    setData: (state, { payload }) => {
      state.data = DataCombiner(payload.data)
      state.dataByYears = payload.data
      const lastUpdateDate = DateFormatter(new Date())
      state.lastUpdateDate = lastUpdateDate
      localStorage.setItem(`amp_last_${payload.type}`, lastUpdateDate)
    },
    setDataType: (state, action) => {
      state.dataType = action.payload
    },
    setIncrements: (state, action) => {
      state.increments = action.payload
    },
    resetIncrements: (state) => {
      state.increments = InitialIncrementsState
    },
    getDataByYear: (state, action) => {
      const year = action.payload
      if (year in state.dataByYears) {
        state.data = state.dataByYears[year]
      } else {
        state.data = DataCombiner(state.dataByYears)
      }
      state.filterByYear = year
    },
    handleServerError: (state, action) => {
      state.serverError = true
      state.serverErrorMessage = action.payload

      const formerData = JSON.parse(
        localStorage.getItem(`former_${state.dataType}_data`)
      )
      if (formerData) {
        state.data = formerData
        state.isLoggedIn = true
        state.lastUpdateDate = localStorage.getItem(
          `amp_last_${state.dataType}`
        )
      }
    },
    removeServerError: (state) => {
      state.serverError = false
      state.serverErrorMessage = ''
    }
  }
})

export const {
  setCode,
  setPending,
  setIsLoggedIn,
  setData,
  setIncrements,
  resetIncrements,
  getDataByYear,
  setDataType,
  handleServerError,
  removeServerError
} = slice.actions

export default slice.reducer

export function fetchAmplitudeData(type = 'downloads') {
  return async (dispatch) => {
    awaitResponseState(dispatch, type)
    try {
      const response = await API.get(`app-${type}`)
      DataValidator(response.data)
      dispatch(setData({ data: response.data, type }))
      dispatch(setIsLoggedIn(true))
    } catch (error) {
      handleErrorResponse(dispatch, error)
    } finally {
      dispatch(setPending(false))
    }
  }
}

function awaitResponseState(dispatch, type) {
  dispatch(setPending(true))
  dispatch(setDataType(type))
  dispatch(removeServerError())
}

function handleErrorResponse(dispatch, error) {
  if (error.response && error.response.status === 403) {
    dispatch(setIsLoggedIn(false))
  } else {
    let message = error.message
    if (message.includes('429')) message = error419Message
    if (message.includes('Network Error')) message = errorNetwork
    dispatch(handleServerError(message))
  }
}
